import {GET_ALL_RUN_ISSUES, GET_ALL_BUILD_ISSUES, GET_ALL_R7_ISSUES} from './actions'
import {SET_RUN_ISSUES, SET_BUILD_ISSUES, SET_R7_ISSUES} from './mutations'
import {GET_ISSUES_BY_STATUS, GET_ISSUES_BY_SEVERITY, GET_ISSUES_BY_BUILD_APPLICATION, GET_ISSUES_BY_APP_RUN, GET_BUG_SEVERITY_R7,
  GET_ISSUES_TABLE_RUN, GET_ISSUES_TABLE_R7, GET_ISSUES_TABLE_BUILD, GET_SUFFERING_ISSUES, GET_POURCENT_SUFFERING, GET_DUE_DATE_ISSUES} from './getters'

const store = {
  debug: true,
  state: {
    issues: [],
    r7issues: [],
    buildIssues: []
  },
  actions: {
    GET_ALL_RUN_ISSUES,
    GET_ALL_BUILD_ISSUES,
    GET_ALL_R7_ISSUES
  },
  mutations: {
    SET_RUN_ISSUES,
    SET_BUILD_ISSUES,
    SET_R7_ISSUES
  },
  getters: {
    GET_ISSUES_BY_STATUS,
    GET_ISSUES_TABLE_RUN,
    GET_ISSUES_TABLE_R7,
    GET_SUFFERING_ISSUES,
    GET_ISSUES_TABLE_BUILD,
    GET_ISSUES_BY_SEVERITY,
    GET_DUE_DATE_ISSUES,
    GET_ISSUES_BY_BUILD_APPLICATION,
    GET_ISSUES_BY_APP_RUN,
    GET_BUG_SEVERITY_R7,
    GET_POURCENT_SUFFERING
  }
}

export default store



