<template>
  <div>
    <vuestic-widget class="no-padding no-v-padding">
      <vuestic-tabs :names="['Run','Recette', 'Build']" ref="tabs">
        <div slot="Run">
          <run></run>
        </div>
        <div slot="Recette">
          <r7></r7>
        </div> 
        <div slot="Build">
         <build-snippet></build-snippet>
        </div>
      </vuestic-tabs>

    </vuestic-widget>

  </div>
</template>

<script>
  import VuesticWidget from '../vuestic-components/vuestic-widget/VuesticWidget'
  import DashboardInfoWidgets from './DashboardInfoWidgets'
  import Run from './run/Run'
  import loginStore from '../auth/store/store'
  import R7 from './r7/R7'
  import BuildSnippet from './Build/BuildSnippet'

  export default {
    name: 'dashboard',
    components: {
      R7,
      BuildSnippet,
      VuesticWidget,
      Run,
      DashboardInfoWidgets
    },
    mounted () {
      if (!this.loginStore.state.token) {
        window.location.href = '/#/auth/login'
      }
    },
    data () {
      return {
        loginStore: loginStore
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../sass/_variables.scss";
</style>



