
import store from './store'
import {Dispatcher} from '../../Bus'
export const SET_RUN_ISSUES = Dispatcher.$on('SET_RUN_ISSUES', issues => {
  store.state.issues = [...issues]
})
export const SET_BUILD_ISSUES = Dispatcher.$on('SET_BUILD_ISSUES', issues => {
  store.state.buildIssues = [...issues]
})
export const SET_R7_ISSUES = Dispatcher.$on('SET_R7_ISSUES', issues => {
  store.state.r7issues = [...issues]
})



