import lodash from 'lodash'
import randomColor from 'randomcolor'
import store from './store'

/**
 * Getters of run issues to build the charts
 * @returns {{labels: Array, datasets: [null]}}
 * @constructor
 */
export const GET_ISSUES_BY_STATUS = (issuesList) => {
  let categoryCharts = {
    labels: [],
    datasets: [{
      label: 'Stock',
      backgroundColor: [],
      data: []
    }]
  }
  let issues = []
  issuesList.map(issue => {
    transcodeIssueType(issue)
    if (!categoryCharts.labels.includes(issue.fields.status.name)) {
      categoryCharts.labels.push(issue.fields.status.name)
    }
    issues.push(issue.fields.status)
  })
  categoryCharts.labels.map(label => {
    let color = randomColor()
    let data = lodash.filter(issues, {'name': label})
    categoryCharts.datasets[0].data.push(data.length)
    categoryCharts.datasets[0].backgroundColor.push(color)
  })
  return categoryCharts
}

/**
 * Getters of run severity to build the charts
 * @returns {{labels: Array, datasets: [null]}}
 * @constructor
 */
export const GET_ISSUES_BY_SEVERITY = (issuesList) => {
  let categoryCharts = {
    labels: [],
    datasets: [{
      label: 'Sévèrité',
      backgroundColor: [],
      data: []
    }]
  }
  let issues = []
  issuesList.map(issue => {
    if (issue.fields.customfield_10014 === null) {
      issue.fields.customfield_10014 = {value: 'Non catégorisé'}
    }
    if (!categoryCharts.labels.includes(issue.fields.customfield_10014.value)) {
      categoryCharts.labels.push(issue.fields.customfield_10014.value)
    }
    issues.push(issue.fields.customfield_10014)
  })
  categoryCharts.labels.map(label => {
    let color = randomColor()
    let data = lodash.filter(issues, {'value': label})
    categoryCharts.datasets[0].data.push(data.length)
    categoryCharts.datasets[0].backgroundColor.push(color)
  })
  return categoryCharts
}

