/**
 * get issues run by application
 * @returns {{labels: Array, datasets: [null]}}
 * @constructor
 */
export const GET_ISSUES_BY_BUILD_APPLICATION = (issuesList) => {
  let categoryCharts = {
    labels: [],
    datasets: [{
      label: 'Nombre',
      backgroundColor: [],
      data: []
    }]
  }
  let issues = []
  issuesList.map(issue => {
    transcodeBuildType(issue)
  })
  issuesList = lodash.sortBy(issuesList, ['id'])
  issuesList.map(issue => {
    if (issue.fields.status.name === 'NEW') {
      return
    }
    if (!categoryCharts.labels.includes(issue.fields.status.name)) {
      categoryCharts.labels.push(issue.fields.status.name)
    }
    issues.push(issue.fields.status)
  })
  categoryCharts.labels.map(label => {
    let color = randomColor()
    let data = lodash.filter(issues, {'name': label})
    categoryCharts.datasets[0].data.push(data.length)
    categoryCharts.datasets[0].backgroundColor.push(color)
  })
  return categoryCharts
}

export const GET_SUFFERING_ISSUES = issuesList => {
  const sufferingTime = 16
  const result = issuesList.filter((issue, index) => {
    if (issue.fields.aggregatetimespent !== 0 || !issue.fields.aggregatetimespent) {
      issue.fields.aggregatetimespent = Math.floor(issue.fields.aggregatetimespent / 3600)
      if (issue.fields.aggregatetimespent > sufferingTime) {
        return issue.fields.aggregatetimespent > sufferingTime
      }
    }
  }).length
  return result
}

/**
 * Get pourcentage of issues suffering
 * @param issuesList
 * @constructor
 */
export const GET_POURCENT_SUFFERING = issuesList => {
  let pourcentage = issuesList / store.state.issues.length
  pourcentage = pourcentage * 100
  return pourcentage
}

/**
 * Map issues for array dashboard component
 * @returns {Array}
 * @constructor
 */
export const GET_ISSUES_TABLE_RUN = (data) => {
  let arrayTable = []
  data.map(issue => {
    if (issue.fields.components.length > 0) {
      issue.fields.componentsName = issue.fields.components[0].name
    }
    if (!issue.fields.assignee) {
      issue.fields.assignee = {name: 'Non assigné'}
    }
    arrayTable.push(issue.fields)
  })
  return arrayTable
}

/**
 * Map issues for array dashboard component
 * @returns {Array}
 * @constructor
 */
export const GET_ISSUES_TABLE_BUILD = (data) => {
  let arrayTable = []
  data.map(issue => {
    if (issue.fields.status.name === 'NEW') {
      return
    }
    if (issue.fields.components.length > 0) {
      issue.fields.componentsName = issue.fields.components[0].name
    } else {
      return
    }
    if (!issue.fields.assignee) {
      issue.fields.assignee = {name: 'Non assigné'}
    }
    arrayTable.push(issue.fields)
  })
  return arrayTable
}

/**
 * Map issues for array dashboard component
 * @returns {Array}
 * @constructor
 */
export const GET_ISSUES_TABLE_R7 = (data) => {
  let arrayTable = []
  data.map(issue => {
    arrayTable.push(issue.fields)
    if (!issue.fields.assignee) {
      issue.fields.assignee = {name: 'Non assigné'}
    }
  })
  return arrayTable
}

/**
 * get task to do for the month
 */
export const GET_DUE_DATE_ISSUES = (issuesList) => {
  let currentMonth = new Date()
  currentMonth = currentMonth.getMonth()
  let issueToDo = 0
  issuesList.forEach(issue => {
    if (issue.fields.duedate !== null) {
      if (issue.fields.duedate.substring(5, 7) === currentMonth.toString()) issueToDo = issueToDo + 1
    }
  })
  return issueToDo
}

/**
 * Transcode issues
 * @param issue
 */
function transcodeIssueType (issue) {
  if (issue.fields.status.name === 'NEW') {
    issue.fields.status.name = 'À traiter'
  }
  if (issue.fields.status.name === 'REJECTED') {
    issue.fields.status.name = 'En cours'
  }
  if (issue.fields.status.name === 'DOING') {
    issue.fields.status.name = 'En cours'
  }
  if (issue.fields.status.name === 'VALIDATED') {
    issue.fields.status.name = 'En cours'
  }
}

/**
 * transcode build
 * @param issue
 */
function transcodeBuildType (issue) {
  if (issue.fields.status.name === 'TAKEN INTO ACCOUNT' || issue.fields.status.name === 'QUOTED' || issue.fields.status.name === 'VALIDATED') {
    issue.fields.status.name = 'Chiffrage'
    issue.id = 1
  }
  if (issue.fields.status.name === 'STUDIED' || issue.fields.status.name === 'ACCEPTED') {
    issue.fields.status.name = 'Analyse'
    issue.id = 2
  }
  if (issue.fields.status.name === 'STARTED' || issue.fields.status.name === 'DEVELOPED') {
    issue.fields.status.name = 'Développement'
    issue.id = 3
  }
  if (issue.fields.status.name === 'TESTED' || issue.fields.status.name === 'QUALIFIED') {
    issue.fields.status.name = 'Qualification'
    issue.id = 4
  }
  if (issue.fields.status.name === 'DELIVERED') {
    issue.fields.status.name = 'Livré'
    issue.id = 5
  }
}



