import {Dispatcher} from '../../Bus'
import services from './services'

/**
 * Get all run issues from JIRA api
 * @returns {Promise.<void>}
 * @constructor
 */
export const GET_ALL_RUN_ISSUES = async (cs) => {
  await services.getAllRunIssues(cs).then(res => {
    Dispatcher.$emit('SET_RUN_ISSUES', res.issues)
  })
}

/**
 * Get all build issues from JIRA api
 * @returns {Promise.<void>}
 * @constructor
 */
export const GET_ALL_BUILD_ISSUES = async (cs) => {
  await services.getAllBuildIssues(cs).then(res => {
    Dispatcher.$emit('SET_BUILD_ISSUES', res.issues)
  })
}

export const GET_ALL_R7_ISSUES = async (cs) => {
  await services.getAllR7Issues(cs).then(res => {
    Dispatcher.$emit('SET_R7_ISSUES', res.issues)
  })
}



