
<template>
  <div>
          <div class="data-visualisation-tab dashboard-tab">
            <div v-if="donutChartData.labels !== undefined && donutChartData.labels !== undefined" class="row">
              <div class="col-md-12">
                <div class="chart-container">
                  <vuestic-chart v-bind:data="donutChartData" type="horizontal-bar"></vuestic-chart>
                  <br>
                </div>
              </div>

              <div class="col-md-12">
                <data-table v-if="tableData.length > 0" :apiMode="false"
                            :data="this.tableData"
                            :tableFields="tableFields"
                            :itemsPerPage="itemsPerPage"
                            :onEachSide="onEachSide"
                            :paginationPath="paginationPath">
                </data-table>
              </div>
            </div>
            <div v-if="donutChartData.labels === undefined && donutChartData.labels === undefined" class="row">
              <div class="col-md-12">
                <div class="sk-circle">
                  <div class="sk-circle1 sk-child"></div>
                  <div class="sk-circle2 sk-child"></div>
                  <div class="sk-circle3 sk-child"></div>
                  <div class="sk-circle4 sk-child"></div>
                  <div class="sk-circle5 sk-child"></div>
                  <div class="sk-circle6 sk-child"></div>
                  <div class="sk-circle7 sk-child"></div>
                  <div class="sk-circle8 sk-child"></div>
                  <div class="sk-circle9 sk-child"></div>
                  <div class="sk-circle10 sk-child"></div>
                  <div class="sk-circle11 sk-child"></div>
                  <div class="sk-circle12 sk-child"></div>
                </div>
              </div>
            </div>
          </div>
        </div>
  </div>

</template>

<script>
  import DataTable from '../../vuestic-components/vuestic-datatable/VuesticDataTable.vue'
  import VuesticChart from '../../vuestic-components/vuestic-chart/VuesticChart.vue'
  import store from '../store/store'
  import css from '../run/dataTable/dataTable.css'
  export default {
    name: 'buildSnippet',
    props: ['cs'],
    components: {
      DataTable,
      VuesticChart,
      css
    },
    data () {
      return {
        store: store,
        donutChartData: [],
        sortFunctions: '',
        tableData: [],
        onEachSide: 1,
        tableFields: [
          {
            name: 'dvgzg',
            title: '',
            dataClass: 'text-center'
          },
          {
            name: 'customfield_10582',
            title: 'Ref'
          },
          {
            name: 'componentsName',
            title: 'Application'
          },
          {
            name: 'summary',
            title: 'Description',
            sortField: 'summary'
          },
          {
            name: 'status.name',
            title: 'Status'
          },
          {
            name: 'assignee.name',
            title: 'Assignation'
          }
        ],
        paginationPath: 'pagination',
        itemsPerPage: [
          {
            value: 5
          },
          {
            value: 6
          }
        ]
      }
    },
    created () {
      let currentCs = {token: 'Basic bWlzb2ZmcmF5OkxpbGZsb3c4NCo=', jiraId: 'AG2RCSPB_RQM'}
      this.getAllIssuesBuild(currentCs)
      setInterval(() => {
        this.getAllIssuesBuild(currentCs)
      }, 30000)
    },
    methods: {
      getAllIssuesBuild (cs) {
        this.store.actions.GET_ALL_BUILD_ISSUES(cs).then(res => {
          this.donutChartData = this.store.getters.GET_ISSUES_BY_BUILD_APPLICATION([...store.state.buildIssues])
          this.tableData = this.store.getters.GET_ISSUES_TABLE_BUILD([...store.state.buildIssues])
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../../../src/sass/variables";
  @import "../../../../node_modules/bootstrap/scss/variables";
  @import "../../../..//node_modules/bootstrap/scss/mixins/breakpoints";

  .chart-container {
    padding: 0 2rem;
    height: 24rem;
  }

  .table-responsive {
    display: block;
    width: 100%;
    height: 500px;
    overflow-x: auto;
    -ms-overflow-style: -ms-autohiding-scrollbar;
  }

  .sk-circle {
    margin: 100px auto;
    width: 40px;
    height: 40px;
    position: relative;
  }
  .sk-circle .sk-child {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
  }
  .sk-circle .sk-child:before {
    content: '';
    display: block;
    margin: 0 auto;
    width: 15%;
    height: 15%;
    background-color: #333;
    border-radius: 100%;
    -webkit-animation: sk-circleBounceDelay 1.2s infinite ease-in-out both;
    animation: sk-circleBounceDelay 1.2s infinite ease-in-out both;
  }
  .sk-circle .sk-circle2 {
    -webkit-transform: rotate(30deg);
    -ms-transform: rotate(30deg);
    transform: rotate(30deg); }
  .sk-circle .sk-circle3 {
    -webkit-transform: rotate(60deg);
    -ms-transform: rotate(60deg);
    transform: rotate(60deg); }
  .sk-circle .sk-circle4 {
    -webkit-transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    transform: rotate(90deg); }
  .sk-circle .sk-circle5 {
    -webkit-transform: rotate(120deg);
    -ms-transform: rotate(120deg);
    transform: rotate(120deg); }
  .sk-circle .sk-circle6 {
    -webkit-transform: rotate(150deg);
    -ms-transform: rotate(150deg);
    transform: rotate(150deg); }
  .sk-circle .sk-circle7 {
    -webkit-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    transform: rotate(180deg); }
  .sk-circle .sk-circle8 {
    -webkit-transform: rotate(210deg);
    -ms-transform: rotate(210deg);
    transform: rotate(210deg); }
  .sk-circle .sk-circle9 {
    -webkit-transform: rotate(240deg);
    -ms-transform: rotate(240deg);
    transform: rotate(240deg); }
  .sk-circle .sk-circle10 {
    -webkit-transform: rotate(270deg);
    -ms-transform: rotate(270deg);
    transform: rotate(270deg); }
  .sk-circle .sk-circle11 {
    -webkit-transform: rotate(300deg);
    -ms-transform: rotate(300deg);
    transform: rotate(300deg); }
  .sk-circle .sk-circle12 {
    -webkit-transform: rotate(330deg);
    -ms-transform: rotate(330deg);
    transform: rotate(330deg); }
  .sk-circle .sk-circle2:before {
    -webkit-animation-delay: -1.1s;
    animation-delay: -1.1s; }
  .sk-circle .sk-circle3:before {
    -webkit-animation-delay: -1s;
    animation-delay: -1s; }
  .sk-circle .sk-circle4:before {
    -webkit-animation-delay: -0.9s;
    animation-delay: -0.9s; }
  .sk-circle .sk-circle5:before {
    -webkit-animation-delay: -0.8s;
    animation-delay: -0.8s; }
  .sk-circle .sk-circle6:before {
    -webkit-animation-delay: -0.7s;
    animation-delay: -0.7s; }
  .sk-circle .sk-circle7:before {
    -webkit-animation-delay: -0.6s;
    animation-delay: -0.6s; }
  .sk-circle .sk-circle8:before {
    -webkit-animation-delay: -0.5s;
    animation-delay: -0.5s; }
  .sk-circle .sk-circle9:before {
    -webkit-animation-delay: -0.4s;
    animation-delay: -0.4s; }
  .sk-circle .sk-circle10:before {
    -webkit-animation-delay: -0.3s;
    animation-delay: -0.3s; }
  .sk-circle .sk-circle11:before {
    -webkit-animation-delay: -0.2s;
    animation-delay: -0.2s; }
  .sk-circle .sk-circle12:before {
    -webkit-animation-delay: -0.1s;
    animation-delay: -0.1s; }

  @-webkit-keyframes sk-circleBounceDelay {
    0%, 80%, 100% {
      -webkit-transform: scale(0);
      transform: scale(0);
    } 40% {
        -webkit-transform: scale(1);
        transform: scale(1);
      }
  }

  @keyframes sk-circleBounceDelay {
    0%, 80%, 100% {
      -webkit-transform: scale(0);
      transform: scale(0);
    } 40% {
        -webkit-transform: scale(1);
        transform: scale(1);
      }
  }


</style>



