import loginStore from '../../auth/store/store'
import {BASE_URL_JIRA_API} from '../../url-conf'
export default {
  /**
   * Get all run issues from JIRA api
   * @returns {*}
   */
  async getAllRunIssues (cs) {
    let headers = new Headers()
    headers.append('Content-Type', 'application/json')
    let init = { method: 'POST',
      headers: headers,
      mode: 'CORS',
      body: JSON.stringify({applicationId: loginStore.state.concernedApplication, token: loginStore.state.token}),
      'Access-Control-Allow-Origin': '*',
      cache: 'default'}
    try {
      let request = new Request(BASE_URL_JIRA_API + '/jira/rest/api/run', init)
      let response = await fetch(request)
      response = await response.json()
      return response
    } catch (err) {
      return err
    }
  },

  /**
   * Get all run issues from JIRA api
   * @returns {*}
   */
  async getAllBuildIssues (cs) {
    let headers = new Headers()
    headers.append('Content-Type', 'application/json')
    let init = { method: 'POST',
      headers: headers,
      body: JSON.stringify({applicationId: loginStore.state.concernedApplication, token: loginStore.state.token}),
      mode: 'CORS',
      'Access-Control-Allow-Origin': '*',
      cache: 'default'}
    try {
      let request = new Request(BASE_URL_JIRA_API + '/jira/rest/api/build', init)
      let response = await fetch(request)
      response = await response.json()
      return response
    } catch (err) {
      return err
    }
  },

  /**
   * Get all run issues from JIRA api
   * @returns {*}
   */
  async getAllR7Issues (cs) {
    let headers = new Headers()
    headers.append('Content-Type', 'application/json')
    let init = { method: 'POST',
      body: JSON.stringify({applicationId: cs.jiraId, token: loginStore.state.token}),
      headers: headers,
      mode: 'CORS',
      'Access-Control-Allow-Origin': '*',
      cache: 'default'}
    try {
      let request = new Request(BASE_URL_JIRA_API + '/jira/rest/api/r7', init)
      let response = await fetch(request)
      response = await response.json()
      return response
    } catch (err) {
      return err
    }
  }
}



