<template>
  <div class="row dashboard-info-widgets" v-if="store.state.issues.length > 0">
    <div class="col-sm-12 col-md-12 col-xl-2">
      <vuestic-widget class="info-widget">
        <div class="info-widget-inner">
          <div class="stats">
            <div class="stats-number">
              <i class="ion ion-arrow-up-c text-primary stats-icon"></i>
              59
            </div>
            <div class="stats-title">Sortie sur la semaine</div>
          </div>
        </div>
      </vuestic-widget>
    </div>
    <div class="col-sm-12 col-md-12 col-xl-2">
      <vuestic-widget class="info-widget">
        <div class="info-widget-inner">
          <div class="stats">
            <div class="stats-number">
              <i class="ion ion-arrow-down-c text-danger stats-icon"></i>
              12
            </div>
            <div class="stats-title">Entrée sur la semaine</div>
          </div>
        </div>
      </vuestic-widget>
    </div>
    <div class="col-md-6 col-xl-4">
      <vuestic-widget class="info-widget brand-danger">
        <div class="info-widget-inner">
          <div class="info-widget-inner has-chart">
            <div class="stats">
              <div class="stats-number">
                {{sufferingIssue}}
              </div>
              <div class="stats-title">En souffrance</div>
            </div>
            <div class="chart-container">
              <progress-bar type="circle" ref="circleProgress" :colorName="'white'" :backgroundColorName="'danger'"
                            :startColorName="'danger'"></progress-bar>
            </div>
          </div>
        </div>
      </vuestic-widget>
    </div>
    <div class="col-md-6 col-xl-2">
      <vuestic-widget class="info-widget brand-info">
        <div class="info-widget-inner">
          <div class="stats">
            <div class="stats-number">
              <i class="ion ion-android-document stats-icon icon-wide"></i>
{{store.state.issues.length}}
            </div>
            <div class="stats-title">Fiche en stock</div>
          </div>
        </div>
      </vuestic-widget>
    </div>
    <div class="col-md-6 col-xl-2">
      <vuestic-widget class="info-widget brand-info">
        <div class="info-widget-inner">
          <div class="stats">
            <div class="stats-number">
              <i class="ion ion-android-people stats-icon icon-wide"></i>
              {{endMonthIssue}}
            </div>
            <div class="stats-title">Fin de mois</div>
          </div>
        </div>
      </vuestic-widget>
    </div>
  </div>
</template>

<script>
  import ProgressBar from '../../components/vuestic-components/vuestic-progress-bar/VuesticProgressBar'
  import store from './store/store'

  export default {
    name: 'dashboard-info-widgets',
    data () {
      return {
        store: store,
        sufferingIssue: 0,
        endMonthIssue: 0
      }
    },
    components: {
      ProgressBar
    },
    mounted () {
      this.initData()
      setTimeout(() => this.initData(), 30000)
    },
    methods: {
      initData () {
        this.sufferingIssue = this.store.getters.GET_SUFFERING_ISSUES(this.store.state.issues)
        this.$refs.circleProgress.$data.value = this.store.getters.GET_POURCENT_SUFFERING(this.sufferingIssue)
        this.endMonthIssue = this.store.getters.GET_DUE_DATE_ISSUES(this.store.state.issues)
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../sass/_variables.scss";

  .stats-number, .stats-title {
    line-height: 1;
  }

  .info-widget-inner {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    position: relative;
    width: 100%;

    &.has-chart {
      justify-content: space-between;
    }

    .stats {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      height: 100%;
    }
  }

  .stats-number {
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    font-size: 2.625rem;
    margin-bottom: 0.5rem;

    .stats-icon {
      font-size: 1.5625rem;
      position: absolute;
      top: 0.625rem;
      left: -1.25rem;

      &.icon-wide {
        left: -1.875rem;
      }
    }
  }

</style>



